import React from 'react'

function Search({handleSubmit, handleChange,movieYear,movieTitle}) {
  return (
    <form >
        <div className="form-group">
          <input  
          className="form-control" 
          type="text" 
          placeholder="search title ..." 
          onChange={handleChange} 
          name="movieTitle" 
          value={movieTitle}
          />
          <input type="number" 
          className="form-control" 
          placeholder="Movie year..." 
          onChange={handleChange} 
          name="movieYear" 
          value={movieYear}
          />
        </div>
        <div className="form-group">
          <select 
            className="form-control" 
            name="movieType" 
            onChange={handleChange}>
            <option value="movie">movies</option>
            <option value="series">series</option>
            <option value="episode">episode</option>
          </select>
        </div>
        <input className="form-control" type="submit" value="search" onClick={handleSubmit}/>
      </form>
  )
}

export default Search
