import React, { useState, useEffect } from 'react';
import axios from "axios";
import Search from './Search';
import CurrentMovie from './CurrentMovie';

function Main() {
  const [isLoading, setIsLoading] = useState(false);
  const [movieType, setMovieType] = useState();
  const [movieTitle, setMovieTitle] = useState("");
  const [movieYear, setMovieYear] = useState("");
  const [moviePoster, setMoviePoster] = useState();
  const [currentMovie, setCurrentMovie] = useState();
  const [selected, setSelected] = useState([]);
  const [errorMaximumNo, setErrorMaximumNo] = useState();
  const [dashbordNo, setDashbordNo] = useState();
  const [apiError, setApiError] = useState();
  const [apiResponse, setApiResponse] = useState(false);

  let key = "e9ee2678";

  const handleChange = (e)=>{
    if(e.target.name === "movieType"){
      setMovieType(e.target.value);
    }else if(e.target.name === "movieTitle"){
      setMovieTitle(e.target.value);
    }else if(e.target.name === "movieYear"){
      setMovieYear(e.target.value);
    }
  }

  const handleSubmit = (e)=>{
    e.preventDefault();
    setApiResponse(true);

    const fetchMovies = async ()=> {
      setIsLoading(true);
      const res = await axios.get(`http://www.omdbapi.com/?apikey=${key}&t=${movieTitle}&y=${movieYear}&type=${movieType}`)
      setApiError(res.error);
      setCurrentMovie(res.data);
      setIsLoading(false);
    }
    fetchMovies();
  }

  useEffect(() => {
  }, [apiResponse])

  const handleNominated = ()=>{
    setSelected([...selected, {
      title: currentMovie.Title,
      year: currentMovie.Year,
      plot: currentMovie.Plot,
      genre: currentMovie.Genre
    }])

    console.log(selected);
  }

  return (
    <div className=" container Main">
      <div className="header">
        <header>Masko Movie Award Nomination</header>
        <p>Nominate your favourite movies </p>
      </div>
      < Search 
      handleSubmit={handleSubmit} 
      handleChange={handleChange}
      movieYear={movieYear}
      movieTitle={movieTitle}
      />
      < CurrentMovie 
      currentMovie={currentMovie} 
      handleNominated={handleNominated}
      />
      
    </div>
  )
}

export default Main
