import React from 'react'
import Nomination from './Nomination'

function CurrentMovie({currentMovie, handleNominated}) {
  return (
    <div className="jumbotron">
      <p>{currentMovie? currentMovie.Title:null}</p>
      {currentMovie? currentMovie.Year:null}
      <p>{currentMovie? currentMovie.Genre:null}</p>
      <p>{currentMovie? currentMovie.Plot:null}</p>
      <button className="btn btn-primary btn-block" onClick={handleNominated}>Nominate</button>
    </div>
  )
}

export default CurrentMovie
